
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { usePageBackground } from '@/hooks/usePageBackground';
import { Clapperboard, Film, Loader2 } from 'lucide-react';
import { generateVideoPresentation } from '@/ai/flows/video-presentation-flow';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

export default function VideoPresentationPage() {
  usePageBackground('');
  const [videoData, setVideoData] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleGenerateVideo = async () => {
    setIsLoading(true);
    setVideoData(null);
    setError(null);
    try {
      const result = await generateVideoPresentation();
      if (result.videoDataUri) {
        setVideoData(result.videoDataUri);
      } else {
        setError('The AI could not generate a video at this time. Please try again later.');
      }
    } catch (e: any) {
      console.error(e);
      setError(e.message || 'An unexpected error occurred. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto text-center">
      <Card className="shadow-lg">
        <CardHeader>
          <div className="mx-auto bg-primary/10 p-4 rounded-full w-fit mb-4">
            <Clapperboard className="w-10 h-10 text-primary" />
          </div>
          <CardTitle className="text-3xl font-headline">Generate App Presentation</CardTitle>
          <CardDescription>
            Create a short video presentation showcasing the key features of "My Abacus Pro".
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <Button onClick={handleGenerateVideo} disabled={isLoading} size="lg">
            {isLoading ? (
              <>
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                Generating Video... (this may take a minute)
              </>
            ) : (
              <>
                <Film className="mr-2 h-5 w-5" />
                Generate Presentation
              </>
            )}
          </Button>

          {error && (
            <Alert variant="destructive">
              <AlertTitle>Generation Failed</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {videoData && (
            <div className="mt-8">
              <h3 className="text-2xl font-semibold mb-4">Your Video is Ready!</h3>
              <video
                src={videoData}
                controls
                className="w-full rounded-lg border shadow-lg"
              >
                Your browser does not support the video tag.
              </video>
              <Button asChild className="mt-4">
                 <a href={videoData} download="my-abacus-pro-presentation.mp4">
                    Download Video
                </a>
              </Button>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
